.footer{
    width:100%;
    height:130px;
    background-color:#5E5854;
    margin-top:100px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.footer p{
    color:white;
    font-size:12px;
}
.footer a{
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
}
.footer-link p {
    margin:0px 5px;
    font-size: 12px;
}
.footer img{
    width:33px;
    height:33px;
    color:white;
}